<template>
  <a-table :columns="columns" :dataSource="data" bordered>
    <template
      v-for="col in ['deliverMan', 'deliverDate', 'userName', 'pieceName', 'total', 'owed']"
      :slot="col"
      slot-scope="text, record"
    >
      <div :key="col">
        <a-input
          v-if="record.editable"
          style="margin: -5px 0"
          :value="text"
          @change="e => handleChange(e.target.value, record.key, col)"
        />
        <template v-else>{{text}}</template>
      </div>
    </template>
    <template slot="operation" slot-scope="text, record">
      <div class="editable-row-operations">
        <span v-if="record.editable">
          <a @click="() => save(record.key)">保存</a>
          <a-popconfirm title="确定取消?" @confirm="() => cancel(record.key)">
            <a>Cancel</a>
          </a-popconfirm>
        </span>
        <span v-else>
          <a @click="() => edit(record.key)">编辑</a>
        </span>
        <a-popconfirm v-if="data.length" title="确定删除?" @confirm="() => onDelete(record.key)">
          <a href="javascript:;">删除</a>
        </a-popconfirm>
      </div>
    </template>
  </a-table>
</template>
<script>
const columns = [
  {
    title: "送货人",
    dataIndex: "deliverMan",
    width: "15%",
    scopedSlots: { customRender: "deliverMan" }
  },
  {
    title: "送货时间",
    dataIndex: "deliverDate",
    width: "15%",
    scopedSlots: { customRender: "deliverDate" }
  },
  {
    title: "客户名称",
    dataIndex: "userName",
    width: "20%",
    scopedSlots: { customRender: "userName" }
  },
  {
    title: "稿件名称",
    dataIndex: "pieceName",
    width: "15%",
    scopedSlots: { customRender: "pieceName" }
  },
  {
    title: "结算金额",
    dataIndex: "total",
    width: "10%",
    scopedSlots: { customRender: "total" }
  },
  {
    title: "欠款金额",
    dataIndex: "owed",
    width: "10%",
    scopedSlots: { customRender: "owed" }
  },
  {
    title: "操作",
    dataIndex: "operation",
    scopedSlots: { customRender: "operation" }
  }
];

const data = [];
for (let i = 0; i < 4; i++) {
  data.push({
    key: i.toString(),
    deliverMan: `Edrward ${i}`,
    deliverDate: 32,
    userName: `London Park no. ${i}`
  });
}
export default {
  data() {
    this.cacheData = data.map(item => ({ ...item }));
    return {
      data,
      columns
    };
  },
  methods: {
    handleChange(value, key, column) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    },
    edit(key) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        target.editable = true;
        this.data = newData;
      }
    },
    onDelete(key) {
      const data = [...this.data];
      this.data = data.filter(item => item.key !== key);
    },
    save(key) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        delete target.editable;
        this.data = newData;
        this.cacheData = newData.map(item => ({ ...item }));
      }
    },
    cancel(key) {
      const newData = [...this.data];
      const target = newData.filter(item => key === item.key)[0];
      if (target) {
        Object.assign(
          target,
          this.cacheData.filter(item => key === item.key)[0]
        );
        delete target.editable;
        this.data = newData;
      }
    }
  }
};
</script>
<style scoped>
.editable-row-operations a {
  margin-right: 8px;
}
</style>